import { Component, OnInit, EventEmitter } from '@angular/core';
import {VisitsService} from '../../services/visits.service';
import {Visit} from '../../models/visit';
import {MaterializeAction} from 'angular2-materialize';
import {Router} from '@angular/router';

@Component({
  selector: 'app-visits',
  templateUrl: './visits.component.html',
  styleUrls: ['./visits.component.css']
})
export class VisitsComponent implements OnInit {
  editState = false;
  visitToEdit: Visit;
  visits: Visit[];
  modalActions = new EventEmitter<string|MaterializeAction>();
  constructor(private router: Router, private visitService: VisitsService) { }

  ngOnInit() {
    this.visitService.getVisits().subscribe(visits => {
      console.log(visits);
      this.visits = visits;
    });
  }

  editVisit(event, visit) {
    this.editState = true;
    this.visitToEdit = visit;
  }
  updateVisit(visit: Visit) {
    this.visitService.updateVisit(visit);
    this.clearState();
  }
  clearState() {
    this.editState = false;
    this.visitToEdit = null;
  }
  showVisitDetails(event, visit) {
    alert(visit.id);
    // this.router = Router;
    // this.router.parent.navigate(['/events']);
    this.visitService.selectedVisit(visit);
    this.router.navigate(['events']);
    return '';
  }
  openModal() {
    this.modalActions.emit({action: 'modal', params: ['open']});
  }
  closeModal() {
    this.modalActions.emit({action: 'modal', params: ['close']});
  }
}



