import { Component, OnInit } from '@angular/core';
import {ItemService} from '../../services/item.service';
import {VisitsService} from '../../services/visits.service';
import {Visit} from '../../models/visit';
import {Item} from '../../models/item';

@Component({
  selector: 'app-add-item',
  templateUrl: './add-item.component.html',
  styleUrls: ['./add-item.component.css']
})
export class AddItemComponent implements OnInit {
  item: Item = {
    title: '',
    description: '',
    visitId: '',
    attendees: '',
    duration: 0,
    orderby: 0,
    type: '',
    location: '',
    startTime: '',
    endTime: ''
  };
  visits: Visit[];
  visitName = 'Select Visit';
  visitId = '';
  message: Visit;
  constructor(private itemService: ItemService, private visitsService: VisitsService) { }

  ngOnInit() {
    this.visitsService.getVisits().subscribe(visits => {
      console.log(visits);
      this.visits = visits;
    });
    this.visitsService.currentMessage.subscribe(message => this.message = message);
  }
  onSubmit() {
    console.log('**********************' + this.message.id + '+' + this.item);
    if (this.item.title !== '' && this.item.description !== '' && this.message.id !== '') {
      alert('success - ' + this.item.visitId);
      this.item.visitId = this.message.id;
      this.itemService.addItem(this.item);
      this.item.title = '';
      this.item.description = '';
      this.item.visitId = '';
      this.item.type = '';
      this.item.location = '';
      this.item.attendees = '';
      this.item.duration = 0;
      this.item.orderby = 0;
    }
  }
  selectVisit(event, visit: Visit) {
    console.log(visit);
    this.visitName = visit.title;
    this.visitId = visit.id;
    this.item.visitId = this.visitId;
  }
}



