import { Component, OnInit, OnDestroy, IterableDiffers, EventEmitter } from '@angular/core';
import {Router} from '@angular/router';
import {Subscription} from 'rxjs/Subscription';
import {TimerObservable} from 'rxjs/observable/TimerObservable';
import { DomSanitizer } from '@angular/platform-browser';
import { DragulaService } from 'ng2-dragula/ng2-dragula';
import {MaterializeAction} from 'angular2-materialize';
import * as moment from 'moment';
import {ItemService} from '../../services/item.service';
import {VisitsService} from '../../services/visits.service';
import {Item} from '../../models/item';
import {Visit} from '../../models/visit';
@Component({
  selector: 'app-item',
  templateUrl: './item.component.html',
  styleUrls: ['./item.component.css']
})
export class ItemComponent implements OnInit, OnDestroy {
  items: Item[];
  visits: Visit[];
  editState = false;
  reorderState = false;
  dateTime: any = {
    date: '',
    time: '',
    duration: ''
  };
  displayTime: any = {
    startTime: '',
    endTime: '',
    date: ''
  };
  itemToEdit: Item;
  enableDrag: false;
  modalActions = new EventEmitter<string|MaterializeAction>();
  message: Visit;
  // tslint:disable-next-line:max-line-length
  private subscription: Subscription;
  // tslint:disable-next-line:max-line-length
  constructor(private router: Router, private iteSmervice: ItemService, private visitsService: VisitsService , private dragula: DragulaService, private domSanitizer: DomSanitizer) {
    const bag: any = this.dragula.find('bag-items'); if (bag !== undefined) { this.dragula.destroy('bag-items'); }
   }
// tslint:disable-next-line:use-life-cycle-interface

  ngOnInit() {
    console.log('ngOnInit ran!' + this.message);
    const timer = TimerObservable.create(200, 100);
    this.subscription = timer.subscribe(t => {
      console.log('+++++++++++++++++++' + t);
      if (t === 10) {
        this.subscription.unsubscribe();
        if (this.message.id === undefined) {
          this.router.navigate(['visits']);
        }
      }
    });
    this.visitsService.getVisits().subscribe(visits => {
      console.log(visits);
      this.visits = visits;
    });
    this.visitsService.currentMessage.subscribe(message => this.message = message);
   // this.visitsService.getVisitDetailsById(this.message);
    this.iteSmervice.getItemByVisitId(this.message.id);
    this.iteSmervice.getItem().subscribe(items => {
      console.log(items);
      this.items = items;
      // this.processReordering(this.items);
    });
    this.dragula
      .drag
      .subscribe(value => {
        console.log(`Dragging the ${ value[1].innerText}!`);
    });

    this.dragula
      .drop
      .subscribe(value => {
        // console.log(`Dropped the ${ value[1].innerText }!`);
        console.log(this.items);
        this.reorderState = true;
      });
      this.dragula.setOptions('bag-items', {
        revertOnSpill: true,
        moves: function (el: any, container: any, handle: any): any {
          // debugger
          console.log(el.classList);
          if (el.classList.contains('CLASS_HERE')) {
            return false;
          }
            return true;
        }
      });

  }
  deleteItem(event, item) {
    alert('dbl click');
    this.clearState();
    this.iteSmervice.deleteItem(item);
  }

  editItem(event, item) {
    this.editState = true;
    this.itemToEdit = item;
  }
  updateItem(item: Item) {
    this.iteSmervice.updateItem(item);
    this.clearState();
  }
  clearState() {
    this.editState = false;
    this.itemToEdit = null;
  }
  saveTheOrder() {
    this.processReordering(this.items);
  }
  processReordering(items: Item []) {
    let temp = 0;
    // tslint:disable-next-line:no-unused-expression

    for (let i = 0; i < items.length; i++) {
      temp = i + 1;
      // alert(i + ', ' + items[i].orderby);
     // if (temp !== Number(items[i].orderby)) {
        this.items[i].orderby = i + 1;
        if (i === 0) {
          this.displayTime = this.timeFormater(this.message.startDate, this.message.startTime, items[i].duration);
          items[i].startTime = this.displayTime.startTime;
          items[i].endTime = this.displayTime.endTime;
          alert(items[i].startTime + '' + items[i].endTime);
        }else {
          this.displayTime = this.timeFormater(this.displayTime.date, this.displayTime.endTime, items[i].duration);
          items[i].startTime = this.displayTime.startTime;
          items[i].endTime = this.displayTime.endTime;
        }
        this.updateItem(this.items[i]);
     // }
    }
    this.reorderState = false;
  }
  openModal() {
    this.modalActions.emit({action: 'modal', params: ['open']});
  }
  closeModal() {
    this.modalActions.emit({action: 'modal', params: ['close']});
  }
  filterEvents() {
    alert('cool');
    // ls7fhCjlKou2inpMPW7t
    // C3Qy1RYc6ol349pHbkS5
    // visit: Visit = this.message
    this.iteSmervice.getItemByVisitId(this.message.id);
    this.iteSmervice.getItem().subscribe(items => {
      console.log(items);
      this.items = items;
    });
  }
  onSubmit() {
    // const a = moment(moment(this.dateTime.date + ' ' + this.dateTime.time)).add('minutes', 7);
    // alert(moment(a).format('hh:mm A'));
   // alert(a.hour() + ':' + a.minutes() + ':' + a.seconds());
    // tslint:disable-next-line:radix
  const myObject: any = this.timeFormater(this.dateTime.date, this.dateTime.time, parseInt(this.dateTime.duration));
  alert(myObject.startTime + ',' + myObject.endTime);
  }
  timeFormater(date: string, time: string, duration: number) {
    // tslint:disable-next-line:label-position
    const displayTime = {
      startTime: '',
      endTime: '',
      date: ''
    };
    const originalTime = moment(date + ' ' + time);
    const modifiedTime = moment(moment(date + ' ' + time)).add(duration, 'minutes');
    displayTime.startTime = moment(originalTime).format('hh:mm A');
    displayTime.endTime = moment(modifiedTime).format('hh:mm A');
    displayTime.date = moment(modifiedTime).format('YYYY-MM-DD');
    alert('cool' + moment(modifiedTime).format('YYYY-MM-DD'));

    return displayTime ;
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }
}



